import {row, col, toCSS} from '../utils'

class Block {
    constructor(value, options) {
        this.value = value
        this.options = options
    }

    toHTML() {
        throw new Error('–ú–µ—Ç–æ–¥ toHTML –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å —Ä–µ–∞–ª–∏–∑–æ–≤–∞–Ω –≤ –∫–∞–∂–¥–æ–º –∫–ª–∞—Å—Å–µ!')
    }
}

export class TitleBlock extends Block {
    constructor(value, options) {
        super(value, options)
    }

    toHTML() {
        const {tag = 'h1', styles} = this.options
        // const tag = block.options.tag ?? 'h1'
        // const styles = block.options.styles
        return row(col(`<${tag} class="header" style="${toCSS(styles)}">${this.value}</${tag}>`))
    }
}

export class ParagraphBlock extends Block {
    constructor(value, options) {
        super(value, options)
    }

    toHTML() {
        const styles = this.options.styles
        return row(col(`<p class="paragraph" style="${toCSS(styles)}">${this.value}</p>`))
    }
}

export class ColumnsBlock extends Block {
    constructor(value, options) {
        super(value, options)
    }

    toHTML() {
        const styles = this.options.styles
        const html = this.value.map(item => col(item, styles))   // –ê–Ω–∞–ª–æ–≥–∏—á–Ω–æ .map(item => col(item))
        // –ü–æ–ª—É—á–∏–ª–∏ map –∏ —Å–æ–µ–¥–∏–Ω—è–µ–º –µ–≥–æ —ç–ª–µ–º–µ–Ω—Ç—ã —Å –ø–æ–º–æ—â—å—é join(), –∏–∑–±–∞–≤–ª—è—è—Å—å –æ—Ç –∑–∞–ø—è—Ç—ã—Ö –ø—Ä–∏ –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–æ–º –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–∏ –≤ —Å—Ç—Ä–æ–∫—É
        return row(`<p>${html.join('')}</p>`)
    }
}

export class ImageBlock extends Block {
    constructor(value, options) {
        super(value, options)
    }

    toHTML() {
        const {styles, alt, divStyles} = this.options
        return row(`<img alt="${alt}" src="${this.value}" style="${toCSS(styles)}"/>`, divStyles)
    }
}

export class TextBlock extends Block {
    constructor(value, options) {
        super(value, options)
    }

    toHTML() {
        const{styles, divStyles} = this.options
        return row(`<p style="${toCSS(styles)}">${this.value}</p>`, divStyles)
    }
}